* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    background: linear-gradient(to right, #22c1c3, #fdbb2d);
    font-family: 'Raleway', sans-serif;
    color: rgb(73, 73, 73);
}

.container {
    position: absolute;
    min-width: 80%;
    //margin: 2em auto;
    top: 2%;
    left: 10%;
    //transform: translateX(-50%);
}


.search {
    color: inherit;
    padding: 12px 10px;
    min-width: 100%;
    border: none;
    outline: none;
}

datalist {
    background: #fff;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.2);
    transition: .4s height;
    position: absolute;
    max-height: 40em;
    min-width: 100%;
    //left: 10%;
    border: 0 none;
    overflow-x: hidden;
    overflow-y: auto;
}

datalist option {
    font-size: 13px;
    padding: 10px 10px;
    border-top: 1px solid #ddd;
    background-color: #ccc;
    cursor: pointer;
}

datalist option:hover {
    background-color: #f1f1f1
}

.movies {
    position: relative;
    z-index: -1;
    color: #fff;
}

.movies ul {
    list-style: none;
    padding: 1em 0;
}